<script setup>
import { ref,inject,onMounted } from 'vue'
import { Dialog, DialogPanel, DialogTitle, TransitionChild, TransitionRoot } from '@headlessui/vue'
import { ArrowRight,X} from 'lucide-vue-next'

const open = ref(false)
const open_ass = ref(false)
const policyconsent = ref(false)
const call = inject('$call')
const auth = inject('$auth')
const store = inject('store')
const last_draft = ref({})

const openDialog = () => {
    open.value = true;
}
const assessment = () => {
    open.value = false;
    open_ass.value = true;
}
const set_policyconsent = async () => {
	const res = await call('pwit.controllers.api.set_policyconsent_session', {
		name:store.session,
		value:policyconsent.value
	});
	if (res.code === 200) {
		console.log(res)
        // policyconsent.value=res.data
	}
};
const get_last_draft = async () => {
	const res = await call('pwit.controllers.api.get_last_draft', { });
	if (res.code === 200) {
        console.log(res.data)
        last_draft.value = res?.data ?? {}
	}
};
onMounted(async()=>{
    if(auth.isLoggedIn){
       await get_last_draft()
    }
})
</script>