<template>
  <div v-if="!field.hidden" class="flex flex-col gap-2">
    <div class="flex items-center">
      <label :for="field.name" class="text-sm font-medium text-gray-700 dark:text-gray-200">
        {{ field.label }} <span v-if="isFieldMandatory(field)" class="text-red-500 ml-1">*</span>
      </label>
      <div v-if="field.description" class="ml-2 relative">
        <Popover v-slot="{ open }" class="relative">
          <PopoverButton @mouseenter="open = true" @mouseleave="open = false" class="focus:outline-none">
            <InfoIcon class="w-4 h-4 text-gray-400 hover:text-gray-600 dark:text-gray-500 dark:hover:text-gray-300" />
          </PopoverButton>

          <transition enter-active-class="transition duration-200 ease-out" enter-from-class="opacity-0 translate-y-1"
            enter-to-class="opacity-100 translate-y-0" leave-active-class="transition duration-150 ease-in"
            leave-from-class="opacity-100 translate-y-0" leave-to-class="opacity-0 translate-y-1">
            <PopoverPanel class="absolute z-10 w-96 px-4 mt-3 transform -translate-x-1/2 left-1/2 sm:px-0 lg:max-w-3xl"
              @mouseenter="open = true" @mouseleave="open = false">
              <div class="overflow-hidden rounded-lg shadow-lg ring-1 ring-black ring-opacity-5">
                <div class="p-4 bg-white dark:bg-gray-800">
                  <p class="text-sm text-gray-700 dark:text-gray-300">
                    {{ field.description }}
                  </p>
                </div>
              </div>
            </PopoverPanel>
          </transition>
        </Popover>
      </div>
    </div>
    <div class="relative">
      <input 
        :id="field.name" 
        :value="displayValue" 
        @input="handleInput"
        @focusout="handleBlur" 
        :type="inputType" 
        :disabled="field.read_only"
        :required="isFieldMandatory(field)" 
        :placeholder="field.placeholder" 
        :class="[
          'w-full h-10 px-3 border rounded-md text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-colors duration-200',
          'dark:bg-gray-700 dark:text-gray-200 dark:border-gray-600 dark:focus:ring-blue-400 dark:focus:border-blue-400',
          { 'pr-10': field.fieldtype === 'Password' },
          { 'border-red-500 focus:ring-red-500 focus:border-red-500': error }
        ]" 
        :aria-invalid="!!error"
        :aria-describedby="error ? `${field.name}-error` : undefined"
      />
    </div>
    <p v-if="error" :id="`${field.name}-error`" class="mt-1 text-sm text-red-600 dark:text-red-400" role="alert">{{ error }}</p>
  </div>
</template>

<script setup>
import { ref, inject, computed } from 'vue'
import { Popover, PopoverButton, PopoverPanel } from '@headlessui/vue'
import { InfoIcon } from 'lucide-vue-next'

const props = defineProps({
  field: {
    type: Object,
    required: true
  },
  modelValue: {
    type: String,
    required: true
  },
  onfieldChange: {
    type: Boolean,
    default: false
  },
  formData: {
    type: Object,
    default: () => ({})
  }
})

const error = ref('');
const emit = defineEmits(['update:modelValue'])
const saveAsDraft = inject('saveAsDraft')

const inputType = computed(() => props.field.fieldtype === 'Int' ? 'number' : 'text')

const displayValue = computed(() => {
  if (props.field.fieldtype === 'Int') {
    const numValue = parseInt(props.modelValue);
    return isNaN(numValue) || numValue < 0 ? '' : props.modelValue;
  }
  return props.modelValue;
})

const isFieldMandatory = (field) => {
  if (field.reqd) return true
  if (!field.mandatory_depends_on) return false
  const condition = field.mandatory_depends_on.replace('eval:', '').replace(/doc\./g, 'formData.')
  try {
    return new Function('formData', `return ${condition}`)(props?.formData)
  } catch (error) {
    console.error('Error evaluating field visibility:', error)
    return false
  }
}

const validateURL = (url) => {
  const urlPattern = /^(https?:\/\/)?([\w\d\-]+\.)+[a-z]{2,}(\/.*)?$/i;
  return urlPattern.test(url);
};

const handleInput = (event) => {
  let value = event.target.value;
  if (props.field.fieldtype === 'Int') {
    const numValue = parseInt(value);
    if (isNaN(numValue) || numValue < 0) {
      error.value = 'Please enter a non-negative integer.';
      emit('update:modelValue', '');
    } else {
      error.value = '';
      emit('update:modelValue', numValue.toString());
    }
  } else {
    emit('update:modelValue', value);
  }
}

const handleBlur = (event) => {
  const value = event.target.value;

  if (props.field.fieldname === 'organisation_website' && value) {
    if (!validateURL(value)) {
      error.value = 'Please enter a valid URL.';
      return;
    }
  }

  if (props.field.fieldtype === 'Int') {
    const numValue = parseInt(value);
    if (isNaN(numValue) || numValue < 0) {
      error.value = 'Please enter a non-negative integer.';
      emit('update:modelValue', '');
    } else {
      error.value = '';
      emit('update:modelValue', numValue.toString());
    }
  } else {
    error.value = '';
    emit('update:modelValue', value);
  }

  if (props.onfieldChange && !error.value) {
    saveAsDraft({ [props.field.fieldname]: value })
  }
}
</script>

<style scoped>
.w-96 {
  width: 100% !important;
  max-width: 800px !important;
  min-width: 500px !important;
}
</style>

